.button {
    height: 50px;
    border-radius: 8px;
    font-weight: 500;
    background: #8354fd;
    color: white;
    padding: 0 32px;

    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    border: 0;

    transition: 0.2s;

    img {
        margin-right: 8px;
    }

    &.outlined {
        background: #fff;
        border: 1px solid #8354fd;
        color: #8354fd;
    }

    &:not(:disabled):hover {
        filter: brightness(0.9);
    }

    &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
    }
}

.log-out {
    background: #fff;
}